@import url("https://fonts.googleapis.com/css2?family=Playfair+Display:wght@700&family=Poppins:wght@300;400&display=swap");

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}


/* font-family: 'Poppins', sans-serif; */
/* font-family: 'Playfair Display', serif; */

/* //navbar css */
.navbar {
  font-family: "Poppins", sans-serif;
  font-size: 17px;
  line-height: 26px;
  text-transform: uppercase;
  box-shadow: 0 8px 6px -6px gray;
  --webkit-box-shadow: 0 8px 6px -6px gray;
  border-bottom: solid gray !important;
 
}

.nav-link {
  font-weight: 300 !important;
  color: #98504B;
}

.active {
  border-bottom: 2px solid #98504B;
}

.navbar-brand {
  font-weight: 700;
  font-family: "roboto", sans-serif;
  letter-spacing: 3px;
}





/* =============================
==========footer============= */

.footer {
  color: white;
  padding: 25px;
  background: #000000; /* fallback for old browsers */
  background: -webkit-linear-gradient(
    to right,
    #210D10,
    #802201
  ); /* Chrome 10-25, Safari 5.1-6 */
  background:linear-gradient(#210D10,#802201);
}

.footer a {
  text-decoration: none;
  color: white;
  padding: 10px;
}

.footer a:hover {
  color: #ffefba;
  border-bottom: 1px solid #ffefba;
}
/* =================================
======== page not found css ======= */
.pnf {
  text-align: center;
  margin-top: 10vh;
  margin-bottom: 50px;
  
}

.pnf-title {
  color: #ff6347;
  font-size: 6em;
  margin-bottom: 10px;
  
}

.pnf-heading {
  color: #98504B;
  font-size: 2em;
  margin-bottom: 20px;
}

.pnf-img {
  width: 80%;
  max-width: 400px;
  height: auto;
  margin-bottom: 20px;
}

.pnf-btn {
  
  padding: 10px 20px;
  background: linear-gradient(#D58936, #69140E);
  color: #fff;
  text-decoration: none;
  border-radius: 5px;
  border-color:#98504B;
  margin-top: 30px;
  
  
}

.pnf-btn:hover {
  background: white;
  border: 2px solid #98504B;
  color: #000000;
}

@media only screen and (max-width: 600px) {
  .pnf-title {
    font-size: 4em;
    margin-top: 20vh;
  }

  .pnf-heading {
    font-size: 1.5em;
  }
}
/* ========================================= */
/* =========contact us ========= */
.contactus {
  align-items: center;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  min-height: 60vh;
  margin-top: 70px;
}

.contact-details h2 {
  color: #98504b;
  font-size: 50px;
  font-weight: 700;
  margin-bottom: 20px;
}

.contact-details h3 {
  color: #7b5727;
  font-size: 30px;
  font-weight: 300;
  margin-bottom: 20px;

}

.contact-details h3 strong {
  color: #ad615b;
  font-weight: 600;
}

.contact-details h3 .email-contact {
  color: #7b5727;
  font-weight: 300;
  text-decoration: none;
}
.contact-image img {
  border-style: none;
  vertical-align: middle;
  
}
@media screen and (max-width: 1032px){
.contact-details h3 {
    font-size: 20px;
}}
@media screen and (max-width: 1032px){
.contactus {
    flex-direction: column;
    margin-top: 5px;
}
}
@media screen and (max-width: 360px){
  .contactus {
      margin: 0 auto; 
  }
  }

@media screen and (max-width: 1032px){
.contact-details {
    margin-bottom: 20px;
}
}

@media screen and (max-width: 1032px){
.contact-details h2 {
    font-size: 40px;
}}
@media screen and (max-width: 370px){
  .contact-details h2 {
      margin:25px;
  }}
@media screen and (max-width: 370px){
  .contact-details h3 {
      margin:25px;
  }}
@media screen and (max-width: 768px){
.contact-image {
    display: none;
}}

