/* home page css*/

.carousel-inner{
  width:100%;
  max-height:400px !important;
  
}
.carousel-item img{
  max-height: 400px !important;
  
}

.home-page h1 {
  color: gray;
  font-family: "Playfair Display", serif;
}

.home-page {
  display: flex;
  flex-wrap: wrap;
}
.home-page .card {
  background-color: #FEFEEF;
  width: 18rem;
  
}
.search-card{
  background-color: #FEFEEF;
  width: 18rem;
}
.home-page .card-name-price {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.home-page .card-price {
  color: green;
  font-weight: bold;
}
.home-page button {
  border-radius: 5;
  width: 100%;
  font-size: 14px;
  font-weight: bold;
  
}

.home-page .card-text {
  color: rgb(90, 89, 89);
}

.home-page .card img:hover {
  transform: scale(0.9);
}
.search-card img:hover{
  transform: scale(0.9);
}
.search-result{
  margin-top: 80px;
}
.home-page .card-img-top {
  height: 300px !important;
}
