    flex-direction: column;
}
}

@media screen and (max-width: 1032px){
.aboutus-content {
    width: 100%;
}
}

@media screen and (max-width: 1032px){
  .aboutus-content h4 {
      font-size: 15px;
  }
  }

  .policy-header {
    align-items: center;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    min-height: 10vh;
    padding: 20px;
    margin-top: 80px;
    color: #98504b;
}

  .privacy-policy {
    max-width: 1200px;
    margin: 0 auto;
    padding: 20px;
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    justify-content: space-between;
  }

  
  .privacy-policy h2,
  .privacy-policy h3,
  .privacy-policy h4,
  .privacy-policy h5 {
    color:#ad615b 
  }

  .privacy-policy img {
    max-width: 100%;
    height: auto;
    display: block;
    
    margin-bottom: 20px;
  }

  .privacy-policy-content {
    width: 45%; /* Adjust as needed */
    margin-bottom: 20px;
    text-align: justify;

  }

  .privacy-policy-content section {
    padding: 20px;
    
   
  }

  ul {
    list-style-type: disc;
    padding:5px;
    color: #123b38;
  }

  li {
    margin-bottom: 5px;
    display: list-item;
  }

  @media only screen and (max-width: 768px) {
    .privacy-policy-content {
      width: 100%;
      
    }
  }

  

@media screen and (max-width: 768px){
.privacy-policy-content img {
    display: none;
}
}

.table-container {
  overflow-x: auto;
}

.table {
  width: 100%;
  border-collapse: collapse;
}

.table th,
.table td {
  padding: 8px;
  text-align: left;
  border-bottom: 1px solid #ddd;
}

/* Media query for responsiveness */
@media (max-width: 768px) {
  .table th,
  .table td {
    font-size: 14px;
  }
}

.heading{
  margin-top: 80px;
}

@media (max-width: 768px) {
  .heading{
    margin-top:100px;
  }
}

