/* ===================================
========= register page  */
.form-container {

  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 70vh;
  flex-direction: column;
}

.form-container form {
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
  padding: 20px;
  background-color: #fff;
}

.form-container form .title {
  text-align: center;
  margin-bottom: 15px;
  font-weight: bold;
  font-family: "Playfair Display", serif;
  color:#98504B;
}

.form-container form input {
  border: none;
  border-bottom: 1px solid #000;
  border-radius: 0;
}

.form-container form button {
  border: 1px solid #98504B ;
  border-radius: 5;
  background:linear-gradient(#D58936, #69140E);
  color: white;
  width: 250px;
}

.form-container form button:hover {
  border: 1px solid white;
  background: white; /* fallback for old browsers */
  background: -webkit-linear-gradient(
    to right,
    #EE82EE,
    #720e9e
  ); /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(
    to right,
    #EE82EE,
    #720e9e
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.forgot-btn {
  background-color: #434343a4 !important;
  border: 0 !important;
}

.forgot-btn:hover {
  color: white !important;
}

.welcome-content{
  color: #98504B;
  text-align: justify;
}

.welcome-content img{
  margin-bottom: 40px;
}

@media screen and (max-width: 768px){
.welcome-content img {
    height: 230px;
    width: 259px;
    margin-top: 30px;
}
}

@media screen and (max-width: 768px){
  .form-container {
      margin-bottom: -150px;
      margin-top: -50px;
  }
  }

  @media screen and (max-width: 768px){
  .welcome-content h1 {
      font-size: 1.5rem;
      margin-top: 30px;
  }
}
@media screen and (max-width: 768px){
  .welcome-content h4{
      font-size: 1.5rem;
      margin-top: 30px;
      text-align: justify;
  }
}

@media screen and (max-width: 768px){
.welcome-content h6 {
    font-size: .8rem!important;

}}

@media screen and (max-width: 768px){
  .content {
      
      margin-top: -150px;
  }
  }

  @media screen and (max-width: 768px){
    .rgs {
        
        margin-top: -100px;
        margin-bottom: 1px;
    }
    }