.category {
    flex-wrap: wrap;
    margin-top: 80px !important;
  }
  .category .card {
    background-color: #FEFEEF;
    width: 18rem;
  }
  .category .card-name-price {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }
  
  .category .card-price {
    color: green;
    font-weight: bold;
  }
  .category button {
    border-radius: 5;
    width: 100%;
    font-size: 14px;
    font-weight: bold;
  }
  
  .category .card-text {
    color: rgb(90, 89, 89);
  }
  
  .category .card img:hover {
    transform: scale(0.9);
  }
  
  .category .card-img-top {
    height: 300px !important;
  }

  @media screen and (max-width: 370px){
    .category h4 {
        margin-top: 110px;
    }
    }